<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">Final Ticket Offer</h1>
          <h1 class="subtitle is-2">May 2019</h1>
        </div>
      </div>
    </section>

    <section class="container section-spacing content">
      <div class="columns is-centered">
        <div class="column is-7">
          <p class="is-size-4">
            Hi
            <span class="has-text-primary">{{ personName }}</span>
          </p>

          <p>
            Please update your Ticket information.
          </p>

          <p>
            <strong class="has-text-primary">
              The TEDxAuckland team
            </strong>
          </p>
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="updateInfo">
            <h2 class="title is-3">Your Contact Info</h2>
            <PersonInfo class="section-spacing" :person="person" :$v="$v" />

            <TicketDetailsForm
              class="section-spacing"
              :questions="ticketDetails"
              :$v="$v"
            />

            <div class="buttons has-addons is-right">
              <input
                class="button is-primary is-medium"
                type="submit"
                value="Update info"
              />
            </div>
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import PersonInfo from "@/ticketing/components/PersonInfo";
import TicketDetailsForm from "@/ticketing/components/TicketDetailsForm.vue";
import TicketService from "@/services/TicketService";
import NProgress from "nprogress";
import { personNameMixin } from "@/mixins/personNameMixin";
import {
  personQuestionsMixin,
  personQuestionsValidations,
  ticketDetailsMixin,
  ticketDetailsValidations
} from "@/mixins/questionsMixin";

export default {
  name: "TicketDetails",
  components: {
    PersonInfo,
    TicketDetailsForm
  },
  props: {
    personInit: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    },
    event: {
      type: Object,
      required: true
    }
  },
  mixins: [personQuestionsMixin, ticketDetailsMixin, personNameMixin],
  validations: {
    person: personQuestionsValidations,
    ticketDetails: ticketDetailsValidations
  },
  methods: {
    snackbar() {
      this.$snackbar.open(
        `Default, positioned bottom-right with a green 'OK' button`
      );
    },
    updateInfo() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();
        TicketService.updatePersonInfo(this.ticketOffer.id, {
          contactQuestions: this.person,
          personInfo: this.ticketDetails
        })
          .then(() => {
            NProgress.done();
            this.$snackbar.open(`Successfully updated Ticket Details`);
          })
          .catch(e => {
            NProgress.done();
            console.log("ERROR", e);
          });
      }
    }
  }
};
</script>

<style>
.link {
  text-decoration: underline;
}
</style>
